import React from "react";
import PropTypes from "prop-types";

const TableHeader = ({ selectedSort, onSort, columns }) => {
  const handleSort = (item) => {
    if (selectedSort.path === item) { //    item
      onSort({
        ...selectedSort, //     
        order: selectedSort.order === "asc" //    
          ? "desc" //  ,  .   asc     item
          : "asc" //       asc
      });
    } else {
      onSort({ path: item, order: "asc" });//   item  
    }
  };
  const renderSortArrow = (selectedSort, currentPath) => {
    if (selectedSort.path === currentPath) {
      if (selectedSort.order === "asc") {
        return <i className="bi bi-caret-down-fill"></i>;
      } else {
        return <i className="bi bi-caret-up-fill"></i>;
      }
    }
    return null;
  };

  return (
    <thead>
      <tr>
        {Object.keys(columns).map((column) => (
          <th key={column} onClick={columns[column].path
            ? () => handleSort(columns[column].path)
            : undefined} //   - ,   -  
          {...{ role: columns[column].path && "button" }}
          //   - ,    
          scope="col">
            {columns[column].name} {renderSortArrow(selectedSort, columns[column].path)}
          </th>
        ))}
        {/* <th onClick={() => handleSort("name")} scope="col"></th> */}
        {/* <th scope="col"></th> */}
        {/* <th onClick={() => handleSort("profession.name")} scope="col"></th> */}
        {/* <th onClick={() => handleSort("completedMeetings")} scope="col">, </th> */}
        {/* <th onClick={() => handleSort("rate")} scope="col"></th> */}
        {/* <th onClick={() => handleSort("bookmark")} scope="col"></th> */}
        {/* <th scope="col"></th> */}
      </tr>
    </thead>
  );
};

TableHeader.propTypes = {
  selectedSort: PropTypes.object.isRequired,
  onSort: PropTypes.func.isRequired,
  columns: PropTypes.object.isRequired
};

export default TableHeader;
