import _ from "lodash";

export function paginate(items, pageNumber, pageSize) {
  const startIndex = (pageNumber - 1) * pageSize;
  return _(items).slice(startIndex).take(pageSize).value();
}
/*
items -   ,
pageNumber -   (currentPage(1)),
pageSize -   (2);
startIndex -   ,      (0);
.slice -      -  ,  
startIndex;
.take() -       pageSize;
value() -  lodash    .
 */
